import React from 'react';

import LanguageCodeListForm from './LanguageCodeListForm';

class LanguageCodeListPage extends React.Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-md-12 well">
            <LanguageCodeListForm {...this.state} />
          </div>
        </div>
      </div>
    );
  }
}
LanguageCodeListPage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default LanguageCodeListPage;



