import React from 'react';
import axios from 'axios';

import classnames from 'classnames';
import TextFieldGroup from '../../common/TextFieldGroup';
// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/codelist/LanguageCodeList.json');

// console.log(resolvedSchema);

class LanguageCodeListForm extends React.Component {
    constructor() {
        super();
        this.state = {
          language: '',
            schema: {
              enum: [],
              enumNames: []
            }
        };
        this.onChange = this.onChange.bind(this);
    }

    componentDidMount() {
      axios
        .get('http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/codelist/LanguageCodeList#')
        .then(({ data })=> {
        	this.setState({
            schema: data
          });
        })
        .catch((err)=> {})
        // console.log('axios', this.state.data);
    }

    onChange(e) {
      this.setState({ [e.target.name]: e.target.value });
    }

    render() {
      const _enum = this.state.schema.enum;
      const _enumNames = this.state.schema.enumNames;
      const options = _enumNames.map(function(val,i){
        return (
          <option key={_enum[i]} value={_enum[i]}>{val}</option>
        );
      });
      // console.log('enumNames', resolvedSchema.enumNames);
      // console.log(Intl.DateTimeFormat.supportedLocalesOf(resolvedSchema.enum));

        return (
            <div>
              <h2>{this.state.schema.title}</h2>
              <p>{this.state.schema.description}</p>
              <div className={classnames('form-group')}>
                <label className="control-label">Language, Country</label>
                <select
                  className='form-control'
                  name='language'
                  onChange={this.onChange}
                  value={this.state.language}
                >
                  <option value='' disabled>Choose Your Country and Language</option>
                  {options}
                </select>
              </div>
              <TextFieldGroup
                label="Language-Country Code"
                placeholder="en-US"
                onChange={this.onChange}
                value={this.state.language}
                field="language"
              />

            </div>

        );
    }
}

export default LanguageCodeListForm;



