import React from 'react';

/*
Parent component needs to provide this data structure:

const pageInfo = {
  heading: "EntityType",
  description: "Describes an entity with id, name and code.",
  hostedSchema: 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/base/EntityType#'
}
*/

class ComponentPageHeader extends React.Component {

  render() {
    const {heading, description, hostedSchema} = this.props;
    return (
      <div className='row text-center'>
        <h2>{heading}</h2>
        <p>{description}</p>
        <div className='btn-toolbar'>
          <div className='btn-group'>
              <a href={hostedSchema}
              target='_new'
              className='btn btn-primary btn-sm'>
              <span className='glyphicon glyphicon-list-alt'></span> View Schema</a>
          </div>
          <div className='btn-group'>
              <button className='btn btn-sm'>
                <span className='glyphicon glyphicon-folder-open'></span> Supports Drop File
              </button>
            </div>
        </div>
        <hr/>
      </div>
    );
  }
}
export default ComponentPageHeader;



