import React from 'react';
import classnames from 'classnames';
import TextFieldGroup from '../../common/TextFieldGroup';
import axios from 'axios';
// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/codelist/UnitCodeList.json');

// console.log(resolvedSchema);
const schemaUrl = 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/codelist/UnitCodeList#';

class UnitCodeListForm extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        code: '',
        schema: {
          enum: [],
          enumNames: []
        }
      };
  }

  componentDidMount() {
    axios
      .get(schemaUrl)
      .then(({ data })=> {
        console.log(data);
      	this.setState({
          schema: data
        });
      })
      .catch((err)=> {})
      // console.log('axios', this.state.data);
  }

  onChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  }

  render() {
    const _enum = this.state.schema.enum;
    const _enumNames = this.state.schema.enumNames;
    const options = _enumNames.map(function(val,i){
      return (
        <option key={_enum[i]} value={_enum[i]}>{val}</option>
      );
    });
    // console.log('enumNames', enumNames);
    const { title, description } = this.state.schema;
    return (
      <div>
        <h2>{title}</h2>
        <p>{description}</p>
        <a href={schemaUrl} target='_new'>View the schema {title}</a>
        <hr/>

        <div className={classnames('form-group')}>
          <label className="control-label">Unit Code Name</label>
          <select
            className='form-control'
            name='code'
            onChange={this.onChange}
            value={this.state.code}
          >
            <option value='' disabled>Choose Unit</option>
            {options}
          </select>
        </div>
        <TextFieldGroup
          label="Unit Code Value"
          placeholder="2I"
          onChange={this.onChange}
          value={this.state.code}
          field="code"
        />

      </div>
    );
  }
}

export default UnitCodeListForm;



