import React from 'react';
import PropTypes from 'prop-types';

import DropLoader from '../../utils/DropLoader';
import EntityTypeForm from './EntityTypeForm';
import ComponentPageHeader from '../../common/ComponentPageHeader';
import ShowJsonText from '../../common/ShowJsonText';
// import SchemaViewer from '../../common/SchemaViewer.js';
// const resolvedSchema = require('json-schema-loader!../../../schema/common/json/base/EntityType.json');

const pageInfo = {
  heading: "EntityType",
  description: "Describes an entity with id, name and code.",
  hostedSchema: 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/base/EntityType#'
}

class EntityTypePage extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        ...props,
        data: {
          code: "",
          id: {
            description: "",
            value: "",
            schemeId: ""
          },
          name: ""
        }
      }
  }
  handleOnChange = (e) => {
    this.setState({
      data: {...this.state.data, [e.target.name]: e.target.value}
    });
  }

  handleClearForm = () => {
    var data = { value: "" };
    this.setState({ data: data });
  }

  handleDropFileInput = (data) => {
    this.setState({ data: data });
  }


  render() {
    return (
      <div>
        <ComponentPageHeader {...pageInfo} />

        <div className="row">
          <div className="col-md-6 well bg-success" style={{marginRight: "5px"}}>
            <EntityTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
              {...this.state} />
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data} />
          </div>
          <DropLoader onChange={this.handleDropFileInput}/>
          {/* <SchemaViewer value={resolvedSchema} /> */}
        </div>
      </div>
    );
  }
}
EntityTypePage.propTypes = {
  code: PropTypes.string,
  name: PropTypes.string,
  id: PropTypes.object
};

export default EntityTypePage;



