import React from 'react';
import classnames from 'classnames';
import TextFieldGroup from '../../common/TextFieldGroup';
import axios from 'axios';
// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/codelist/CountryCodeList.json');

// console.log(resolvedSchema);
const schemaUrl = 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/codelist/CountryCodeList#';
class CountryCodeListForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        country: '',
        schema: {
          enum: [],
          enumNames: []
        }
      };
    }

    componentDidMount() {
      axios
        .get(schemaUrl)
        .then(({ data })=> {
        	this.setState({
            schema: data
          });
        })
        .catch((err)=> {})
        console.log('axios', this.state.data);
    }

    onChange = (e) => {
      this.setState({ [e.target.name]: e.target.value });
    }

    render() {
      const _enum = this.state.schema.enum;
      const _enumNames = this.state.schema.enumNames;
      const options = _enumNames.map(function(val,i){
        return (
          <option key={_enum[i]} value={_enum[i]}>{val}</option>
        );
      });
      // console.log('enumNames', resolvedSchema.enumNames);
      // console.log(Intl.DateTimeFormat.supportedLocalesOf(resolvedSchema.enum));
      const { title, description } = this.state.schema;
        return (
            <div>
              <h2 className='text-center'>{title}</h2>
              <p>{description}</p>
              <a href={schemaUrl} target='_new'>View the schema {title}</a>
              <hr/>
              <div className={classnames('form-group')}>
                <label className="control-label">Country</label>
                <select
                  className='form-control'
                  name='country'
                  onChange={this.onChange}
                  value={this.state.country}
                >
                  <option value='' disabled>Choose Your Country</option>
                  {options}
                </select>
              </div>
              <TextFieldGroup
                label="Country Code"
                placeholder="US"
                onChange={this.onChange}
                value={this.state.country}
                field="country"
              />

            </div>

        );
    }
}

export default CountryCodeListForm;



