import React from 'react';

import DropLoader from '../../utils/DropLoader';
import ComponentPageHeader from '../../common/ComponentPageHeader';
import ShowJsonText from '../../common/ShowJsonText';
import MeasureTypeForm from './MeasureTypeForm';

const pageInfo = {
  heading: "MeasureType",
  description: "A geographical coordinate",
  hostedSchema: 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/base/MeasureType#'
}

class MeasureTypePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      data: {
        value: 0,
        codeType: ''
      }
    }
  }
  handleOnChange = (e) => {
    this.setState({
      data: {
        ...this.state.data,
        [e.target.name]: e.target.value
      }
    });
  }

  handleClearForm = () => {
    var data = {
      value: 0, unitCode: ""
    };
    this.setState({data: data});
  }

  handleDropFileInput = (data) => {
    this.setState({data: data});
  }
  render() {
    return (
      <div>

        <ComponentPageHeader {...pageInfo} />

        <div className="row">
          <div className="col-md-6">
            <MeasureTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
              {...this.state} />
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data} />
          </div>
        </div>

        <DropLoader onChange={this.handleDropFileInput}/>
    </div>
    );
  }
}
MeasureTypePage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default MeasureTypePage;



