import React from 'react';

import CountryCodeListForm from './CountryCodeListForm';


class CountryCodeListPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageInfo: {
        heading: "CountryCodeList",
        description: "Two letter ISO Country codelist",
        hostedSchema: 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/codelist/CountryCodeList#'
      }
    }
  }

  render() {
    return (
      <div>
        <div className="row">
          <div className="col-md-12 well">
            <CountryCodeListForm {...this.state} />
          </div>
        </div>
      </div>
    );
  }
}
CountryCodeListPage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default CountryCodeListPage;



