import React from 'react';

import DropLoader from '../../utils/DropLoader';
import IdentifierTypeForm from './IdentifierTypeForm';
import ComponentPageHeader from '../../common/ComponentPageHeader';
import ShowJsonText from '../../common/ShowJsonText';
// import SchemaViewer from '../../common/SchemaViewer.js';
// import SchemaValidator from '../../common/SchemaValidator';

// const schema = require('json-schema-loader!../../../schema/common/json/base/IdentifierType.json');
const pageInfo = {
  heading: 'IdentifierType',
  description: 'An identifier is a character string used to uniquely identify one instance of an object within an identification scheme that is managed by an agency. Use this type when the list or values are controlled by an external entity, the list or values are public and could be referenced or validated.',
  hostedSchema: 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/base/IdentifierType#'
}

class IdentifierTypePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      data: {
        value: ""
      }
    }
  }

  handleOnChange = (e) => {
    this.setState({
      data: {
        ...this.state.data,
        [e.target.name]: e.target.value
      }
    });
  }

  handleClearForm = () => {
    var data = {
      description: "",
      value: "",
      schemeId: "",
      schemeVersionId: "",
      schemeLink: "",
      schemeAgencyId: "",
      agencyUri: ""
    }
    data = {
      value: ""
    };
    this.setState({data: data});
  }

  handleDropFileInput = (data) => {
    this.setState({data: data});
  }

  render() {
    // console.log('schema: ' + schema.title);
    return (
      <div className='container'>

        <ComponentPageHeader {...pageInfo} />

        <div className="row">
          <div className="col-md-6 well bg-success" style={{marginRight: "5px"}}>
            <IdentifierTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
              {...this.state}/>
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data}/>
          </div>
        </div>
        <DropLoader onChange={this.handleDropFileInput}/> {/* <div className='row'>
          <div className='col-md-3'>
            <SchemaValidator value={this.state.data} schema={schema}/>
          </div>
          <div className='col-md-6'>
            <SchemaViewer value={schema} />
          </div>
        </div> */}
      </div>
    );
  }
}
IdentifierTypePage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default IdentifierTypePage;



