import React from 'react';

import UnitCodeListForm from './UnitCodeListForm';


class UnitCodeListPage extends React.Component {
  constructor() {
      super();
      this.state = {};
  }


  render() {
    return (
      <div>
        <div className="row">
          <div className="col-md-12 well">
            <UnitCodeListForm {...this.state} />
          </div>
        </div>
      </div>
    );
  }
}

export default UnitCodeListPage;



