import React from 'react';

import DropLoader from '../../utils/DropLoader';
import GeoTypeForm from './GeoTypeForm';
import ComponentPageHeader from '../../common/ComponentPageHeader';
import ShowJsonText from '../../common/ShowJsonText';

const pageInfo = {
  heading: "GeoType",
  description: "A geographical coordinate",
  hostedSchema: 'http://hropen.us-west-2.elasticbeanstalk.com/schema-dev/json/common/base/GeoType#'
}
//https://en.wikipedia.org/wiki/Decimal_degrees
// const schema = require('json-schema-loader!../../../schema/common/json/base/GeoType.json');

class GeoTypePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      data: {
        latitude: 0,
        longitude: 0,
        altitude: { value: 0}
      }
    }
  }

  handleOnChange = (e) => {
    this.setState({
      data: {
        ...this.state.data,
        [e.target.name]: e.target.value
      }
    });
  }

  handleClearForm = () => {
    var data = {
      latitude: 0,
      longitude: 0,
      altitude: { value: 0, unitCode: ""}
    }
    this.setState({data: data});
  }

  handleDropFileInput = (data) => {
    this.setState({data: data});
  }

  render() {
    // console.log('schema: ' + schema.title);
    return (
      <div>

        <ComponentPageHeader {...pageInfo} />

        <div className="row">
          <div className="col-md-6 col-lg-6 well bg-success">
            <GeoTypeForm
              handleOnChange={this.handleOnChange}
              handleClearForm={this.handleClearForm}
              {...this.state} />
          </div>
          <div className="col-md-3 well">
            <ShowJsonText value={this.state.data} />
          </div>
          <DropLoader onChange={this.handleDropFileInput}/>
          {/* <SchemaViewer value={resolvedSchema} /> */}
        </div>
      </div>
    );
  }
}

export default GeoTypePage;



